import React, { useState, useMemo } from 'react';
import { motion } from 'framer-motion';
import {
  FiEdit,
  FiTrash2,
  FiUserPlus,
  FiLogIn,
  FiUpload,
  FiDownload,
  FiSettings,
  FiFilter,
  FiCalendar,
  FiUser,
} from 'react-icons/fi';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

// Mock activity data
const mockActivities = [
  {
    id: 1,
    user: 'John Doe',
    action: 'edit',
    description: 'Modified user settings',
    target: 'Settings',
    timestamp: new Date(2024, 2, 15, 14, 30),
    userAvatar: 'JD',
  },
  {
    id: 2,
    user: 'Sarah Smith',
    action: 'create',
    description: 'Created new user account',
    target: 'Users',
    timestamp: new Date(2024, 2, 15, 12, 45),
    userAvatar: 'SS',
  },
  {
    id: 3,
    user: 'Mike Johnson',
    action: 'delete',
    description: 'Deleted project files',
    target: 'Projects',
    timestamp: new Date(2024, 2, 14, 16, 20),
    userAvatar: 'MJ',
  },
  {
    id: 4,
    user: 'Emily Brown',
    action: 'login',
    description: 'Logged into the system',
    target: 'Authentication',
    timestamp: new Date(2024, 2, 14, 9, 15),
    userAvatar: 'EB',
  },
  {
    id: 5,
    user: 'John Doe',
    action: 'upload',
    description: 'Uploaded monthly report',
    target: 'Documents',
    timestamp: new Date(2024, 2, 13, 17, 45),
    userAvatar: 'JD',
  },
  {
    id: 6,
    user: 'Sarah Smith',
    action: 'download',
    description: 'Downloaded financial report',
    target: 'Reports',
    timestamp: new Date(2024, 2, 13, 11, 30),
    userAvatar: 'SS',
  },
  {
    id: 7,
    user: 'Mike Johnson',
    action: 'settings',
    description: 'Updated system settings',
    target: 'System',
    timestamp: new Date(2024, 2, 12, 15, 20),
    userAvatar: 'MJ',
  },
];

const ActivityLog = () => {
  const [dateRange, setDateRange] = useState([null, null]);
  const [startDate, endDate] = dateRange;
  const [selectedUsers, setSelectedUsers] = useState([]);
  const [selectedActions, setSelectedActions] = useState([]);
  const [isFilterOpen, setIsFilterOpen] = useState(false);

  const uniqueUsers = useMemo(() => {
    return [...new Set(mockActivities.map(activity => activity.user))];
  }, []);

  const actionTypes = [
    { type: 'edit', icon: FiEdit, label: 'Edit' },
    { type: 'create', icon: FiUserPlus, label: 'Create' },
    { type: 'delete', icon: FiTrash2, label: 'Delete' },
    { type: 'login', icon: FiLogIn, label: 'Login' },
    { type: 'upload', icon: FiUpload, label: 'Upload' },
    { type: 'download', icon: FiDownload, label: 'Download' },
    { type: 'settings', icon: FiSettings, label: 'Settings' },
  ];

  const getActionIcon = (action) => {
    const actionType = actionTypes.find(type => type.type === action);
    return actionType ? actionType.icon : FiEdit;
  };

  const filteredActivities = useMemo(() => {
    return mockActivities
      .filter(activity => {
        if (startDate && endDate) {
          const activityDate = new Date(activity.timestamp);
          return activityDate >= startDate && activityDate <= endDate;
        }
        return true;
      })
      .filter(activity => {
        if (selectedUsers.length === 0) return true;
        return selectedUsers.includes(activity.user);
      })
      .filter(activity => {
        if (selectedActions.length === 0) return true;
        return selectedActions.includes(activity.action);
      })
      .sort((a, b) => b.timestamp - a.timestamp);
  }, [startDate, endDate, selectedUsers, selectedActions]);

  const formatDate = (date) => {
    return new Intl.DateTimeFormat('en-US', {
      month: 'short',
      day: 'numeric',
      hour: 'numeric',
      minute: 'numeric',
      hour12: true,
    }).format(date);
  };

  return (
    <div className="max-w-6xl mx-auto py-6 sm:px-6 lg:px-8">
      <div className="bg-white dark:bg-gray-800 shadow rounded-lg">
        <div className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-2xl font-bold text-gray-900 dark:text-white">Activity Log</h1>
            <button
              onClick={() => setIsFilterOpen(!isFilterOpen)}
              className="flex items-center px-4 py-2 text-sm font-medium text-gray-700 dark:text-gray-200 bg-gray-100 dark:bg-gray-700 rounded-md hover:bg-gray-200 dark:hover:bg-gray-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
            >
              <FiFilter className="mr-2" />
              Filters
            </button>
          </div>

          {/* Filters Section */}
          <motion.div
            initial={false}
            animate={{ height: isFilterOpen ? 'auto' : 0, opacity: isFilterOpen ? 1 : 0 }}
            transition={{ duration: 0.3 }}
            className="overflow-hidden mb-6"
          >
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 p-4 bg-gray-50 dark:bg-gray-700 rounded-lg">
              {/* Date Range Filter */}
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  <FiCalendar className="inline mr-2" />
                  Date Range
                </label>
                <DatePicker
                  selectsRange={true}
                  startDate={startDate}
                  endDate={endDate}
                  onChange={(update) => setDateRange(update)}
                  className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-800 dark:text-white"
                  placeholderText="Select date range"
                />
              </div>

              {/* User Filter */}
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  <FiUser className="inline mr-2" />
                  Users
                </label>
                <div className="space-y-2">
                  {uniqueUsers.map(user => (
                    <label key={user} className="flex items-center">
                      <input
                        type="checkbox"
                        checked={selectedUsers.includes(user)}
                        onChange={(e) => {
                          if (e.target.checked) {
                            setSelectedUsers([...selectedUsers, user]);
                          } else {
                            setSelectedUsers(selectedUsers.filter(u => u !== user));
                          }
                        }}
                        className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                      />
                      <span className="ml-2 text-sm text-gray-700 dark:text-gray-300">{user}</span>
                    </label>
                  ))}
                </div>
              </div>

              {/* Action Type Filter */}
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Action Types
                </label>
                <div className="space-y-2">
                  {actionTypes.map(({ type, icon: Icon, label }) => (
                    <label key={type} className="flex items-center">
                      <input
                        type="checkbox"
                        checked={selectedActions.includes(type)}
                        onChange={(e) => {
                          if (e.target.checked) {
                            setSelectedActions([...selectedActions, type]);
                          } else {
                            setSelectedActions(selectedActions.filter(a => a !== type));
                          }
                        }}
                        className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                      />
                      <Icon className="ml-2 h-4 w-4 text-gray-500 dark:text-gray-400" />
                      <span className="ml-2 text-sm text-gray-700 dark:text-gray-300">{label}</span>
                    </label>
                  ))}
                </div>
              </div>
            </div>
          </motion.div>

          {/* Activity List */}
          <div className="space-y-4">
            {filteredActivities.map((activity) => {
              const ActionIcon = getActionIcon(activity.action);
              return (
                <motion.div
                  key={activity.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  className="flex items-start space-x-4 p-4 bg-gray-50 dark:bg-gray-700 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-600 transition-colors"
                >
                  <div className="flex-shrink-0">
                    <div className="w-10 h-10 rounded-full bg-blue-100 dark:bg-blue-900 flex items-center justify-center">
                      <span className="text-sm font-medium text-blue-600 dark:text-blue-300">
                        {activity.userAvatar}
                      </span>
                    </div>
                  </div>
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between">
                      <p className="text-sm font-medium text-gray-900 dark:text-white">
                        {activity.user}
                      </p>
                      <div className="flex items-center">
                        <ActionIcon className="h-4 w-4 text-gray-500 dark:text-gray-400 mr-2" />
                        <time className="text-xs text-gray-500 dark:text-gray-400">
                          {formatDate(activity.timestamp)}
                        </time>
                      </div>
                    </div>
                    <p className="mt-1 text-sm text-gray-500 dark:text-gray-300">
                      {activity.description}
                    </p>
                    <p className="mt-1 text-xs text-gray-400 dark:text-gray-500">
                      Target: {activity.target}
                    </p>
                  </div>
                </motion.div>
              );
            })}

            {filteredActivities.length === 0 && (
              <div className="text-center py-8 text-gray-500 dark:text-gray-400">
                No activities found matching your filters
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ActivityLog; 